<script>
import { WebCam } from "vue-web-cam";
// import { saveAs } from 'file-saver'

  export default {
    name: 'TakePictureComponent',
    components: {
        "vue-web-cam": WebCam
    },
     data() {
        return {
            img: null,
            camera: null,
            deviceId: null,
            devices: [],
            resolutionObj: null
        };
    },
    computed: {
        device: function() {
            return this.devices.find(n => n.deviceId === this.deviceId);
        },
        resolution () {
          return this.resolutionObj
        }
    },
    watch: {
        camera: function(id) {
            this.deviceId = id;
        },
        devices: function() {
            // Once we have a list select the first one
            const [first, ...tail] = this.devices;
            console.log('tail' + tail)
            if (first) {
              const length = this.devices.length
              if (length > 1 ) {
                this.camere = this.devices[length - 1].deviceId
                this.deviceId = this.devices[length - 1].deviceId
              } else {
                this.camera = first.deviceId;
                this.deviceId = first.deviceId;
                }
            }
        }
    },
    mounted () {
      this.resolutionObj = new Object()
       this.resolutionObj.height = 1080
       this.resolutionObj.width = 1920
    },
    methods: {

        clear () {
          this.img = null
          sessionStorage.clear()
          location.reload()
        },
        onCapture() {
            this.img = this.$refs.webcam.capture();
            sessionStorage.setItem('code', this.img);
            console.log(this.$refs.webcam.getCanvas())
        },
        onStarted(stream) {
            console.log("On Started Event", stream);
        },
        onStopped(stream) {
            console.log("On Stopped Event", stream);
        },
        onStop() {
            this.$refs.webcam.stop();
        },
        onStart() {
            this.$refs.webcam.start();
        },
        onError(error) {
            console.log("On Error Event", error);
        },
        onCameras(cameras) {
            this.devices = cameras;
            console.log(this.devices)
            console.log("On Cameras Event", cameras);
        },
        onCameraChange(deviceId) {
            this.deviceId = deviceId;
            this.camera = deviceId;
            console.log("On Camera Change Event", deviceId);
        },
    }
  }
</script>
